import React, { Component } from 'react';
import Project from './Project';
import './Portfolio.css';

class Portfolio extends Component {
  render() {
    return (
      <div id='portfolio' className='section'>
        <h1>My Projects</h1>
        <div className='title-line'></div>
        <div className='projects'>
          <Project name='Newflix' tech='React.js' pic='./images/newflix-shot.jpg' pos='10' />
          <Project name='Southern Pet Market' tech='JavaScript + SASS' pic='./images/southern-pet-shot.jpg' pos='50' />
          <Project name='Get Healthy with Greg' tech='Pure JavaScript' pic='./images/get-healthy-shot.jpg' pos='50' />
          <Project name='Random Quote Generator' tech='React.js' pic='./images/random-quote-shot.jpg' pos='50' />
          <Project name='IMDb Watchlist Random Movie Generator' tech='Pure JavaScript' pic='./images/imdb-random-shot.jpg' pos='35' />
          <Project name='Pomodoro Timer' tech='React.js' pic='./images/pomodoro-shot.jpg' pos='50' />
        </div>
      </div>
    )
  }
}

export default Portfolio;